// В этом файле должны быть стили для БЭМ-блока contacts, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.contacts {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding-top: 42px;

  @media (min-width: 660px) {
    padding-top: 63px;
    padding-bottom: 355px;
  }

  @media (min-width: 960px) {
    padding-top: 109px;
    padding-bottom: 477px;
  }

  &__inner {
    @include container(
      $xs-grid-gutter: 0,
      $sm-grid-gutter: 0,
      $md-grid-gutter: 20px
    );
    @include row(
      $xs-grid-gutter: 0,
      $sm-grid-gutter: 0,
      $md-grid-gutter: 0
    );

    justify-content: center;
    text-align: center;
  }

  &__top-content {
    @include col(
      $screen: md,
      $column: 12,
      $gutter: 0
    );

    margin: 0;
    margin-bottom: 38px;

    font-size: 24px;
    font-weight: 300;
    white-space: pre-line;

    @media (min-width: 660px) {
      margin-bottom: 47px;

      font-size: 40px;
    }

    @media (min-width: 960px) {
      margin-bottom: 59px;

      font-size: 50px;
    }
  }

  &__item {
    @include col(
      $screen: md,
      $column: 6,
      $gutter: 10px
    );
    @include col(lg, 4, 12, 0);

    display: flex;
    flex-direction: column;
    margin-bottom: 26px;

    font-size: 24px;
    line-height: 30px;
    font-weight: 300;
    color: $text-color-accent;

    @media (min-width: 660px) {
      margin-bottom: 48px;
    }

    @media (min-width: 960px) {
      &:nth-of-type(2) { // stylelint-disable-line
        order: 1;
      }
    }
  }

  &__info {
    margin-bottom: 3px;

    font-size: 18px;
    line-height: 18px;
    color: $text-color;
    font-weight: 700;

    @media (min-width: 660px) {
      margin-bottom: 8px;
    }

    @media (min-width: 960px) {
      margin-bottom: 26px;
    }
  }

  &__map {
    position: absolute;
    z-index: -1;
    top: 375px;
    width: 100%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;

    background:
      0 -80px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat, // decor
      center / cover url('../img/img-map-mobile.webp') no-repeat; // map

    @media (min-width: $screen-xs) {

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background:
          0 -80px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat,
          center / cover url('../img/img-map-mobile@2x.jpg') no-repeat;
      }

      .no-webpalpha & {
        background:
          0 -80px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat, // decor
          center / cover url('../img/img-map-mobile.jpg') no-repeat; // map
      }
    }

    @media (min-width: $screen-sm) {
      min-height: 400px;
    }

    @media (min-width: 660px) {
      height: 420px;

      background:
        0 -180px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat,
        center / cover url('../img/img-map-mobile@2x.webp') no-repeat;

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background:
          0 -180px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat,
          center / cover url('../img/img-map-main.webp') no-repeat;

        .no-webpalpha & { // stylelint-disable-line max-nesting-depth
          background:
            0 -180px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat,
            center / cover url('../img/img-map-main.jpg') no-repeat;
        }
      }

      .no-webpalpha & {
        background:
          0 -180px / 100% url('../img/bg-triangle-white-tablet.png') no-repeat,
          center / cover url('../img/img-map-mobile@2x.jpg') no-repeat;
      }
    }

    @media (min-width: $screen-lg) {
      top: 410px;
      height: 500px;

      background:
        0 0 / 100% no-repeat url('data:img/png;base64,'),
        center / cover url('../img/img-map-main.webp');

      @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
        background:
          0 0 / 100% no-repeat url('data:img/png;base64,'),
          center / cover url('../img/img-map-main@2x.webp');

        .no-webpalpha & { // stylelint-disable-line max-nesting-depth
          background:
            0 0 / 100% no-repeat url('data:img/png;base64,'),
            center / cover url('../img/img-map-main@2x.jpg');
        }
      }

      .no-webpalpha & {
        background:
          0 0 / 100% no-repeat url('data:img/png;base64,'),
          center / cover url('../img/img-map-main.jpg');
      }
    }
  }
}
